      const colors = ['#FF9B85', '#FFC857', '#84A98C', '#7B9BB3', '#9384A8', '#FF6B6B']
      return colors[Math.floor(Math.random() * colors.length)]
    },
    
    // 
    getFlowerStageClass() {
      if (this.isAnalyzing) return 'stage-analyzing'
      if (this.hasAnalysis) return 'stage-bloomed'
      return 'stage-bud'
    },
    
