    },
    
    // 
    handleImageError(event) {
      console.warn(':', event.target.src)
      // 
      if (!event.target.src.includes('blue-flower')) {
        const stage = this.hasAnalysis ? 'bloom' : 'bud'
        event.target.src = `/assets/images/blue-flower-${stage}.png`
      } else {
