          emoji.className = 'flower-emoji-fallback'
          emoji.textContent = this.hasAnalysis ? '' : ''
          emoji.style.cssText = `
            font-size: 4rem;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
          `
          parent.appendChild(emoji)
        }
      }
    },
    
    //   - DOM
    async generateEmotionBubbles() {
      if (!this.selectedRecord) {
        this.emotionBubbles = []
        return
      }

      const emotions = this.getRecordEmotions(this.selectedRecord)
      if (emotions.length === 0) {
        this.emotionBubbles = []
        return
      }

      // DOM
      await this.$nextTick()

      // 
      const flowerContainer = document.querySelector('.flower-container')
      if (!flowerContainer) {
        console.warn('[] ')
        // 
        setTimeout(() => {
          this.generateEmotionBubbles()
        }, 100)
        return
      }

      const containerRect = flowerContainer.getBoundingClientRect()
      const containerCenterX = containerRect.width / 2
      const containerCenterY = containerRect.height / 2

      // 
      const isMobile = window.innerWidth <= 768
      const isSmallMobile = window.innerWidth <= 480
      
      //  - 
      const flowerRadius = isMobile ? (isSmallMobile ? 35 : 45) : 60 // 
      const spawnRadiusMin = flowerRadius + 15 // 
      const spawnRadiusMax = Math.min(containerCenterX, containerCenterY) - 10 // 
      const spawnAngleRange = [-90, 90] // 
      const maxAttempts = 40 // 
      const maxBubbles = Math.min(emotions.length, isMobile ? 4 : 6) // 

      console.log(`[]  ${maxBubbles} : ${containerRect.width}x${containerRect.height}`)

      const bubbles = []
      const placedRects = [] // 

      // DOM
      this.emotionBubbles = []
      await this.$nextTick()

      // 
      for (let i = 0; i < maxBubbles; i++) {
        const emotion = emotions[i]
        let placedSuccess = false
        let bestPosition = null

        // 
        for (let attempt = 0; attempt < maxAttempts; attempt++) {
          // 
          const randomAngle = spawnAngleRange[0] + Math.random() * (spawnAngleRange[1] - spawnAngleRange[0])
          const randomRadius = spawnRadiusMin + Math.random() * (spawnRadiusMax - spawnRadiusMin)
          
          // 
          const angleRad = (randomAngle - 90) * (Math.PI / 180) // -900
          const x = containerCenterX + randomRadius * Math.cos(angleRad)
          const y = containerCenterY + randomRadius * Math.sin(angleRad)

          // 
          const tempBubble = {
            emotion: emotion,
            x: x,
            y: y,
            style: {
              left: '50%',
              top: '50%',
              transform: `translate(calc(-50% + ${x - containerCenterX}px), calc(-50% + ${y - containerCenterY}px))`,
              '--hue': `${(i * 60 + 30) % 360}deg`,
              animationDelay: `${i * 0.2}s`,
              position: 'absolute',
              zIndex: 10 + i,
              visibility: 'hidden' // 
            },
            class: `detail-bubble-${i % 4}`,
            index: i
          }

          // DOM
          this.emotionBubbles = [...bubbles, tempBubble]
          await this.$nextTick()

          // DOM
          const bubbleElements = document.querySelectorAll('.emotion-bubble')
          if (bubbleElements.length > i) {
            const currentBubbleEl = bubbleElements[i]
            const currentRect = currentBubbleEl.getBoundingClientRect()

            // 
            let hasCollision = false
            for (const placedRect of placedRects) {
              const margin = 5 // 
              if (
                currentRect.left - margin < placedRect.right &&
                currentRect.right + margin > placedRect.left &&
                currentRect.top - margin < placedRect.bottom &&
                currentRect.bottom + margin > placedRect.top
              ) {
                hasCollision = true
                break
              }
            }

            //  - 
            const containerMargin = 5 // 
            const isWithinBounds = 
              currentRect.left >= -containerMargin && // 
              currentRect.right <= containerRect.width + containerMargin && // 
              currentRect.top >= -containerMargin && // 
              currentRect.bottom <= containerRect.height + containerMargin // 

            if (!hasCollision && isWithinBounds) {
              // 
              placedRects.push(currentRect)
              bestPosition = tempBubble
              bestPosition.style.visibility = 'visible'
              placedSuccess = true
              break
            }
          }
        }

        if (placedSuccess && bestPosition) {
          bubbles.push(bestPosition)
          console.log(`[]  "${emotion}" : (${Math.round(bestPosition.x - containerCenterX)}, ${Math.round(bestPosition.y - containerCenterY)})`)
        } else {
          console.warn(`[]  "${emotion}"  ${maxAttempts} `)
          
          // 
          const backupAngle = (i * 120 + 30) % 360 // 
          const backupRadius = spawnRadiusMin + 10 // 
          const backupAngleRad = (backupAngle - 90) * (Math.PI / 180)
          const backupX = containerCenterX + backupRadius * Math.cos(backupAngleRad)
          const backupY = containerCenterY + backupRadius * Math.sin(backupAngleRad)

          // 
          const adjustedX = Math.max(20, Math.min(containerRect.width - 20, backupX))
          const adjustedY = Math.max(20, Math.min(containerRect.height - 20, backupY))

          const backupBubble = {
            emotion: emotion,
            x: adjustedX,
            y: adjustedY,
            style: {
              left: '50%',
              top: '50%',
              transform: `translate(calc(-50% + ${adjustedX - containerCenterX}px), calc(-50% + ${adjustedY - containerCenterY}px))`,
              '--hue': `${(i * 60 + 30) % 360}deg`,
              animationDelay: `${i * 0.2}s`,
              position: 'absolute',
              zIndex: 10 + i,
              visibility: 'visible'
            },
            class: `detail-bubble-${i % 4}`,
            index: i
          }
          bubbles.push(backupBubble)
          console.log(`[]  "${emotion}": (${Math.round(adjustedX - containerCenterX)}, ${Math.round(adjustedY - containerCenterY)})`)
        }
      }

      // 
      this.emotionBubbles = bubbles
      console.log(`[]  ${bubbles.length} `)
    },
    
    
    // 
    getPollenStyle(index) {
      const angle = index * 45
      const distance = 100 + Math.random() * 300
      const x = Math.cos(angle * Math.PI / 180) * distance
      const y = Math.sin(angle * Math.PI / 180) * distance
      
      return {
        left: `calc(50% + ${x}px)`,
        top: `calc(50% + ${y}px)`,
        animationDelay: `${Math.random() * 4}s`,
        animationDuration: `${6 + Math.random() * 4}s`
      }
    },
    
    // 
    formatTime(timestamp) {
      return new Date(timestamp).toLocaleString('zh-CN', {
        month: 'short',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      })
    },
    
    // 
    formatDetailedTime(timestamp) {
      if (!timestamp) return '';
      return new Date(timestamp).toLocaleString('zh-CN', {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
        weekday: 'long',
        hour: '2-digit',
        minute: '2-digit',
        second: '2-digit'
      });
    },
    
    // 
    truncateText(text, length) {
      return text.length > length ? text.substring(0, length) + '...' : text
    },
    
    // 
    sleep(ms) {
      return new Promise(resolve => setTimeout(resolve, ms))
    },
    
    // 
    showError(message) {
      // 
      this.analysisError = message
      
      // 3
      setTimeout(() => {
        this.analysisError = null
      }, 5000)
    },
    
    // 
    generateResponse(userMessage) {
      const responses = [
        '...',
        '',
        '...',
        '...',
        '...'
      ]
      return responses[Math.floor(Math.random() * responses.length)]
    },
    
    async startAnalysis() {
      if (!this.selectedRecord) return;
      
      this.isAnalyzing = true;
      this.analysisError = null;
      this.analysisStage = 0;
      
      // VueLLM
      llmService.setVueInstance(this);
      
      let currentStage = 0;
      
      // 
      const stageInterval = setInterval(() => {
        if (currentStage < this.analysisStages.length - 2) { // 
          currentStage++;
          this.analysisStage = currentStage;
        }
      }, Math.random() * 400 + 600); // 600-1000ms

      try {
        // LLM
        const analysis = await llmService.analyzeThought(this.selectedRecord);
        
        // 
        clearInterval(stageInterval);
        this.analysisStage = this.analysisStages.length - 2;
        
        // 
        await this.sleep(800);
        
        // 
        this.selectedRecord.analysis = analysis;
        
        // 
        this.analysisStage = this.analysisStages.length - 1;
        
        // 
        await this.sleep(600);
        
        // 
        this.selectedRecord.completed = true;
        this.$store.saveState();

        // 100%
        this.analysisStage = this.analysisStages.length;
        
        // 
        await this.sleep(300);
        
      } catch (error) {
        console.error(':', error);
        
        // 
        clearInterval(stageInterval);
        
        // 
        this.analysisError = error.message;
        
        // 
        this.$emit('analysis-error', {
          message: error.message,
          code: error.code,
          details: error.details
        });
        
        // API
        if (error.code === 'INVALID_API_KEY') {
          this.$emit('show-config-dialog');
        }
        
      } finally {
        // 
        clearInterval(stageInterval);
        this.isAnalyzing = false;
        this.analysisStage = 0; // 
      }
    },

    
    // 
    handleResize() {
      if (this.selectedRecord && this.getRecordEmotions(this.selectedRecord).length > 0) {
        // 
        clearTimeout(this.resizeTimeout)
        this.resizeTimeout = setTimeout(() => {
          this.generateEmotionBubbles()
        }, 200)
      }
    },
    
    // 
    buildContextMessage(emotions) {
      const record = this.selectedRecord;
      
      // 
      const timeStr = new Date(record.timestamp).toLocaleString('zh-CN', {
        month: 'short',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
      
      // 
      let contextParts = [
        ' ',
        '',
        ` ****: ${timeStr}`,
        '',
        ` ****: ${emotions.length > 0 ? emotions.join('') : ''}`,
        ''
      ];
      
      // 
      if (record.situation && record.situation.trim()) {
        contextParts.push(` ****: ${record.situation}`);
        contextParts.push('');
      }
      
      // 
      if (record.automaticThought && record.automaticThought.trim()) {
        contextParts.push(` ****: "${record.automaticThought}"`);
        contextParts.push('');
      }
      
      // 
      if (this.analysisResult) {
        if (this.analysisResult.cognitiveBiases && this.analysisResult.cognitiveBiases.length > 0) {
          const biasNames = this.analysisResult.cognitiveBiases.map(bias => bias.label).slice(0, 2);
          contextParts.push(` ****: ${biasNames.join('')}${this.analysisResult.cognitiveBiases.length > 2 ? '' : ''}`);
          contextParts.push('');
        }
      }
      
      contextParts.push(' ');
      
      return contextParts.join('\n');
    },
    
    // 
    generateTopicTitle(record) {
      // 
      const emotion = this.getFirstEmotion(record);
      const thought = record.automaticThought || '';
      
      // 
      if (thought.includes('') || thought.includes('')) {
        return ` ${emotion}`;
      } else if (thought.includes('') || thought.includes('')) {
        return ` ${emotion}`;
      } else if (thought.includes('') || thought.includes('') || thought.includes('')) {
        return ` `;
      } else if (thought.includes('') || thought.includes('')) {
        return ` `;
      } else {
        return ` ${emotion}`;
      }
    },
    
    getPlantIcon(record) {
      if (record.completed) {
        return ''; // 
      } else if (record.analysis) {
        return ''; // 
      } else {
        return ''; // 
      }
    },
    
    getPlantStageClass(record) {
      if (record.completed) return 'stage-bloomed';
      if (record.analysis) return 'stage-growing';
      return 'stage-seed';
    },
    
    getEmotionColor(record) {
      const emotion = this.getFirstEmotion(record);
      const colorMap = {
        '': 'background: linear-gradient(45deg, #FFD700, #FFA500)',
        '': 'background: linear-gradient(45deg, #87CEEB, #4682B4)',
        '': 'background: linear-gradient(45deg, #A0A0A0, #696969)',
        '': 'background: linear-gradient(45deg, #FF6B6B, #FF4757)',
        '': 'background: linear-gradient(45deg, #98FB98, #32CD32)',
        '': 'background: linear-gradient(45deg, #DEB887, #D2B48C)',
        '': 'background: linear-gradient(45deg, #DC143C, #B22222)',
        '': 'background: linear-gradient(45deg, #DDA0DD, #9370DB)'
      };
      
      return colorMap[emotion] || 'background: linear-gradient(45deg, #84A98C, #52796F)';
    },

    getEmotionTagStyle(emotion) {
      const colorMap = {
        '': { background: '#FFF3CD', color: '#856404', border: '#FFD700' },
        '': { background: '#E7F3FF', color: '#1C4E80', border: '#87CEEB' },
        '': { background: '#F8F9FA', color: '#495057', border: '#A0A0A0' },
        '': { background: '#FFEBEE', color: '#C62828', border: '#FF6B6B' },
        '': { background: '#E8F5E8', color: '#2E7D32', border: '#98FB98' },
        '': { background: '#FFF8E1', color: '#8D6E63', border: '#DEB887' },
        '': { background: '#FFEBEE', color: '#B71C1C', border: '#DC143C' },
        '': { background: '#F3E5F5', color: '#6A1B9A', border: '#DDA0DD' },
        '': { background: '#FFF3CD', color: '#856404', border: '#FFD700' },
        '': { background: '#F8F9FA', color: '#495057', border: '#A0A0A0' },
        '': { background: '#FFEBEE', color: '#C62828', border: '#FF6B6B' },
        '': { background: '#FFF3CD', color: '#856404', border: '#FFD700' },
        '': { background: '#FFEBEE', color: '#C62828', border: '#FF6B6B' },
        '': { background: '#F8F9FA', color: '#495057', border: '#A0A0A0' }
      };
      
      const style = colorMap[emotion] || { background: '#E8F4F8', color: '#2D3E40', border: '#84A98C' };
      
      return {
        backgroundColor: style.background,
        color: style.color,
        borderColor: style.border
      };
    },
    
    getGrowthStageText(record) {
      if (record.completed) return '';
      if (record.analysis) return '';
      return '';
    },
    
    getChatCount(record) {
      if (!record.chatTopics) return 0;
      return record.chatTopics.reduce((count, topic) => {
        if (!topic.messages) return count;
        // 
        const userMessages = topic.messages.filter(msg => msg.type === 'user');
        return count + userMessages.length;
      }, 0);
    },
    
    // ===== UI =====
    
    // 
    initializeScrollDetection() {
      // 
      document.addEventListener('scroll', this.onScroll, { passive: true });
      document.addEventListener('wheel', this.onWheel, { passive: true });
      
      // 
      document.addEventListener('touchstart', this.onTouchStart, { passive: true });
      document.addEventListener('touchmove', this.onTouchMove, { passive: true });
      document.addEventListener('touchend', this.onTouchEnd, { passive: true });
      
      // 
      document.addEventListener('keydown', this.onKeyDown);
      
      // 
      this.controlsVisible = false;
    },
    
    // 
    onScroll(event) {
      this.handleUserInteraction();
    },
    
    // 
    onWheel(event) {
      this.handleUserInteraction();
    },
    
    // 
    onTouchStart(event) {
      this.touchStartY = event.touches[0].clientY;
    },
    
    // 
    onTouchMove(event) {
      if (this.touchStartY !== null) {
        const touchY = event.touches[0].clientY;
        const deltaY = Math.abs(touchY - this.touchStartY);
        
        // 5
        if (deltaY > 5) {
          this.handleUserInteraction();
        }
      }
    },
    
    // 
    onTouchEnd(event) {
      this.touchStartY = null;
    },
    
    // 
    onKeyDown(event) {
      const scrollKeys = ['ArrowUp', 'ArrowDown', 'PageUp', 'PageDown', 'Home', 'End', 'Space'];
      if (scrollKeys.includes(event.key)) {
        this.handleUserInteraction();
      }
    },
    
    //  - 
    handleUserInteraction() {
      // 
      this.controlsVisible = true;
      
      // 
      if (this.hideTimeout) {
        clearTimeout(this.hideTimeout);
        this.hideTimeout = null;
      }
      
      // 1
      this.hideTimeout = setTimeout(() => {
        this.controlsVisible = false;
      }, 1000);
    },
    
    // 
    clearTimeouts() {
      if (this.hideTimeout) {
        clearTimeout(this.hideTimeout);
        this.hideTimeout = null;
      }
    },
    
    // UI
    resetUIHideState() {
      // 
      this.controlsVisible = false;
      
      // 
      this.clearTimeouts();
    },
    
    // 
    cleanupScrollDetection() {
      document.removeEventListener('scroll', this.onScroll);
      document.removeEventListener('wheel', this.onWheel);
      document.removeEventListener('touchstart', this.onTouchStart);
      document.removeEventListener('touchmove', this.onTouchMove);
      document.removeEventListener('touchend', this.onTouchEnd);
      document.removeEventListener('keydown', this.onKeyDown);
      
      this.clearTimeouts();
    }
  },
  
  watch: {
    selectedRecord: {
      handler() {
        // 
        this.$nextTick(() => {
          this.generateEmotionBubbles()
        })
      },
      immediate: true
    }
  },
  
  mounted() {
    //  nextTick  store 
    this.$nextTick(() => {
      setTimeout(() => {
        // 
        if (this.$route.query.view === 'overview') {
          // 
          this.selectedRecord = null
          this.isDataLoaded = true
          return
        }
        
        // 
        if (this.$store.state.selectedRecordIndex !== undefined) {
          const index = this.$store.state.selectedRecordIndex
          if (this.$store.state.thoughtRecords && this.$store.state.thoughtRecords[index]) {
            this.selectSeed(index)
          }
        }
        
        // 
        this.isDataLoaded = true
      }, 50) // 
    })
    
    // 
    window.addEventListener('resize', this.handleResize);
    
    // 
    this.initializeScrollDetection();
  },
  
  beforeUnmount() {
    // 
    window.removeEventListener('resize', this.handleResize)
    
    // 
    if (this.resizeTimeout) {
      clearTimeout(this.resizeTimeout)
    }
    
    // 
    this.cleanupScrollDetection();
  }
}
</script>

