  position: absolute;
  padding: 12px 16px;
  background: linear-gradient(135deg, 
    hsl(var(--hue, 200), 70%, 85%), 
    hsl(var(--hue, 200), 60%, 75%)
  );
  color: hsl(var(--hue, 200), 40%, 30%);
  border-radius: 25px;
  font-size: 14px;
  font-weight: 500;
  white-space: nowrap;
  box-shadow: 
    0 8px 25px hsla(var(--hue, 200), 50%, 50%, 0.3),
    0 2px 8px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(15px);
  border: 1px solid hsla(var(--hue, 200), 50%, 80%, 0.5);
  animation: 
    fullscreenBubbleFloat 4s ease-in-out infinite,
    bubbleAppear 0.8s cubic-bezier(0.25, 0.8, 0.25, 1) forwards;
  transform-origin: center;
}

/*  */
.emotion-bubble-fullscreen.preview-bubble-0 {
  --hue: 200deg; /*  */
}

.emotion-bubble-fullscreen.preview-bubble-1 {
  --hue: 280deg; /*  */
}

.emotion-bubble-fullscreen.preview-bubble-2 {
  --hue: 120deg; /*  */
}

.emotion-bubble-fullscreen.preview-bubble-3 {
  --hue: 40deg; /*  */
}

@keyframes fullscreenBubbleFloat {
  0%, 100% {
    transform: translateY(0px) scale(1);
    opacity: 0.8;
  }
  25% {
    transform: translateY(-15px) scale(1.05);
    opacity: 1;
  }
  50% {
    transform: translateY(-8px) scale(1);
    opacity: 0.9;
  }
  75% {
    transform: translateY(-20px) scale(1.02);
    opacity: 0.95;
  }
}

/*  */
.interaction-hint {
  text-align: center;
  margin-top: 2rem;
}

.click-hint {
  color: #52796F;
  font-size: 1.2rem;
  font-style: italic;
  margin: 0 0 1rem 0;
  transition: all 0.3s ease;
}

.fullscreen-flower-area:hover .click-hint {
  color: #84A98C;
  font-size: 1.3rem;
}

.hint-indicator {
  font-size: 2rem;
  animation: gentle-pulse 2s ease-in-out infinite;
  opacity: 0.7;
}

@keyframes gentle-pulse {
  0%, 100% { 
    transform: scale(1); 
    opacity: 0.7; 
  }
  50% { 
    transform: scale(1.2); 
    opacity: 1; 
  }
}

.flower-container {
  position: relative;
  z-index: 1;
  width: 200px;
  height: 200px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.flower-stage {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 160px;
  height: 160px;
  transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 2; /*  */
}

.flower-image {
  position: relative;
  z-index: 1;
  width: 160px;
  height: 160px;
  object-fit: contain;
  filter: drop-shadow(0 10px 30px rgba(0, 0, 0, 0.1));
}

.progress-ring {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 180px;
  height: 180px;
  z-index: 0;
}

.progress-ring circle {
  fill: none;
  stroke-width: 3;
}

.progress-ring .progress-bg {
  stroke: rgba(0, 0, 0, 0.1);
}

.progress-ring .progress-bar {
  stroke: url(#progressGradient);
  transition: stroke-dashoffset 0.5s cubic-bezier(0.4, 0, 0.2, 1);
  stroke-linecap: round;
  filter: drop-shadow(0 2px 4px rgba(132, 169, 140, 0.3));
}

/*  */
.progress-ring defs {
  position: absolute;
}

/* SVG */
.no-svg-gradient .progress-ring .progress-bar {
  stroke: #84A98C;
}

/* =====  ===== */
.emotion-bubbles-container {
  position: absolute;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 1; /*  */
  overflow: visible;
}

.emotion-bubble {
  position: absolute;
  padding: 0.7rem 1rem;
  background: linear-gradient(135deg, 
    hsl(var(--hue, 200), 70%, 85%), 
    hsl(var(--hue, 200), 60%, 75%)
  );
  color: hsl(var(--hue, 200), 40%, 30%);
  border-radius: var(--radius-full, 25px);
  font-size: 0.8rem;
  font-weight: 600;
  white-space: nowrap;
  box-shadow: 
    0 4px 15px hsla(var(--hue, 200), 50%, 50%, 0.3),
    0 2px 8px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid hsla(var(--hue, 200), 50%, 80%, 0.5);
  
  /*  */
  width: var(--bubble-size, auto);
  height: var(--bubble-size, auto);
  min-width: 45px;
  min-height: 32px;
  max-width: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  
  /*  */
  animation: 
    bubbleFloat 6s ease-in-out infinite,
    bubbleAppear 0.6s cubic-bezier(0.25, 0.8, 0.25, 1) forwards;
  
  transform-origin: center;
  transition: all 0.3s ease;
}

.emotion-bubble:hover {
  transform: scale(1.1);
  box-shadow: 
    0 6px 25px hsla(var(--hue, 200), 50%, 50%, 0.4),
    0 4px 15px rgba(0, 0, 0, 0.15);
}

/*  -  */
.emotion-bubble.detail-bubble-0 {
  --hue: 200deg; /*  */
}

.emotion-bubble.detail-bubble-1 {
  --hue: 280deg; /*  */
}

.emotion-bubble.detail-bubble-2 {
  --hue: 120deg; /*  */
}

.emotion-bubble.detail-bubble-3 {
  --hue: 40deg; /*  */
}

/*  */
.emotion-bubble.preview-bubble-0 {
  --hue: 220deg; /*  */
}

.emotion-bubble.preview-bubble-1 {
  --hue: 300deg; /*  */
}

.emotion-bubble.preview-bubble-2 {
  --hue: 140deg; /*  */
}

.emotion-bubble.preview-bubble-3 {
  --hue: 60deg; /*  */
}

/*  */
@keyframes bubbleFloat {
  0%, 100% {
    transform: translateY(0px);
  }
  25% {
    transform: translateY(-8px);
  }
  50% {
    transform: translateY(-4px);
  }
  75% {
    transform: translateY(-12px);
  }
}

@keyframes bubbleAppear {
  0% {
    opacity: 0;
    transform: scale(0.3) translateY(20px);
  }
  60% {
    opacity: 0.8;
    transform: scale(1.1) translateY(-5px);
  }
  100% {
    opacity: 1;
    transform: scale(1) translateY(0px);
  }
}

/*  */
@keyframes bubbleFloat {
  0%, 100% {
    transform: translateY(0px) rotate(var(--random-offset, 0deg)) scale(1);
  }
  25% {
    transform: translateY(-8px) rotate(calc(var(--random-offset, 0deg) + 2deg)) scale(1.02);
  }
  50% {
    transform: translateY(0px) rotate(var(--random-offset, 0deg)) scale(1);
  }
  75% {
    transform: translateY(-4px) rotate(calc(var(--random-offset, 0deg) - 2deg)) scale(0.98);
  }
}

/*  */
@keyframes bubbleGlow {
  0% {
    box-shadow: 
      0 8px 25px rgba(0, 0, 0, 0.15),
      0 2px 8px rgba(132, 169, 140, 0.2),
      inset 0 1px 2px rgba(255, 255, 255, 0.5);
  }
  100% {
    box-shadow: 
      0 12px 35px rgba(0, 0, 0, 0.2),
      0 4px 15px rgba(132, 169, 140, 0.4),
      inset 0 1px 3px rgba(255, 255, 255, 0.7);
  }
}

/*  */
@keyframes bubbleAppear {
  0% {
    opacity: 0;
    transform: translateY(20px) rotate(var(--random-offset, 0deg)) scale(0.3);
  }
  60% {
    opacity: 1;
    transform: translateY(-5px) rotate(var(--random-offset, 0deg)) scale(1.1);
  }
  100% {
    opacity: 1;
    transform: translateY(0px) rotate(var(--random-offset, 0deg)) scale(1);
  }
}

/*  */
.emotion-bubble.bubble-0 {
  animation-duration: 4s, 3s, 0.8s;
  --bubble-hue: 0deg;
}

.emotion-bubble.bubble-1 {
  animation-duration: 4.5s, 3.2s, 1s;
  animation-delay: 0.2s, 0.1s, var(--delay, 0s);
  --bubble-hue: 60deg;
}

.emotion-bubble.bubble-2 {
  animation-duration: 3.8s, 2.8s, 1.2s;
  animation-delay: 0.4s, 0.3s, var(--delay, 0s);
  --bubble-hue: 120deg;
}

.emotion-bubble.bubble-3 {
  animation-duration: 4.2s, 3.5s, 0.9s;
  animation-delay: 0.6s, 0.2s, var(--delay, 0s);
  --bubble-hue: 240deg;
}

/*  */
.emotion-bubble:hover {
  animation-play-state: paused;
  transform: scale(1.15) rotate(var(--random-offset, 0deg));
  background: linear-gradient(135deg, rgba(132, 169, 140, 0.15), rgba(132, 169, 140, 0.1));
  border-color: rgba(132, 169, 140, 0.6);
  box-shadow: 
    0 15px 40px rgba(132, 169, 140, 0.3),
    0 5px 20px rgba(132, 169, 140, 0.25),
    inset 0 2px 4px rgba(255, 255, 255, 0.8);
  z-index: 15;
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}

.flower-hint {
  margin-top: 2rem;
  color: #52796F;
  font-style: italic;
  font-size: 1.1rem;
}

/* =====  ===== */
.error-message {
  margin-top: 2rem;
  animation: slideInUp 0.5s ease-out;
}

.error-content {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  background: linear-gradient(135deg, rgba(255, 99, 71, 0.1), rgba(255, 160, 122, 0.1));
  border: 2px solid rgba(255, 99, 71, 0.3);
  border-radius: 20px;
  padding: 1.5rem;
  backdrop-filter: blur(10px);
  box-shadow: 0 8px 25px rgba(255, 99, 71, 0.15);
}

.error-icon {
  font-size: 1.5rem;
  flex-shrink: 0;
  animation: bounce 2s infinite;
}

.error-text {
  flex: 1;
}

.error-title {
  color: #2D3E40;
  font-size: 1.1rem;
  font-weight: 600;
  margin: 0 0 0.5rem 0;
}

.error-description {
  color: #52796F;
  font-size: 0.95rem;
  line-height: 1.5;
  margin: 0 0 1rem 0;
}

.error-actions {
  display: flex;
  gap: 0.8rem;
  flex-wrap: wrap;
}

.retry-btn, .dismiss-btn {
  padding: 0.6rem 1.2rem;
  border: none;
  border-radius: 15px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 500;
}

.retry-btn {
  background: linear-gradient(135deg, #84A98C, #52796F);
  color: white;
  box-shadow: 0 4px 15px rgba(132, 169, 140, 0.3);
}

.retry-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(132, 169, 140, 0.4);
}

.dismiss-btn {
  background: rgba(132, 169, 140, 0.1);
  color: #52796F;
  border: 1px solid rgba(132, 169, 140, 0.3);
}

.dismiss-btn:hover {
  background: rgba(132, 169, 140, 0.2);
  transform: translateY(-1px);
}

@keyframes slideInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-10px);
  }
  60% {
    transform: translateY(-5px);
  }
}

/* =====  ===== */
.analysis-results {
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
}

.results-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
}

.empathy-card,
.analysis-card,
.exploration-card,
.encouragement-card {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 30px;
  padding: 2.5rem;
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.08);
  backdrop-filter: blur(20px);
}

.card-title {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  color: #2D3E40;
  font-size: 1.6rem;
  font-weight: 500;
  margin-bottom: 2rem;
}

.title-icon {
  font-size: 1.8rem;
}

/*  */
.bias-list {
  display: grid;
  gap: 1rem;
}

.bias-item {
  background: rgba(132, 169, 140, 0.05);
  border-radius: 20px;
  padding: 1.5rem;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  border: 2px solid transparent;
}

.bias-item:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.08);
  border-color: #84A98C;
  background: rgba(132, 169, 140, 0.08);
}

.bias-item.active {
  border-color: #52796F;
  background: rgba(132, 169, 140, 0.12);
}

.bias-header {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 0.8rem;
}

.bias-icon {
  font-size: 1.5rem;
}

.bias-title {
  color: #2D3E40;
  font-size: 1.2rem;
  font-weight: 500;
  margin: 0;
}

.bias-description {
  color: #2D3E40; /*  */
  font-size: 0.95rem;
  line-height: 1.6;
  margin: 0;
  font-weight: 500; /*  */
}

/*  */
.questions-list {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}

.question-item {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  padding: 1.2rem;
  background: rgba(255, 155, 133, 0.05);
  border-radius: 20px;
  border-left: 4px solid #FF9B85;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.question-item:hover {
  transform: translateX(5px);
  background: rgba(255, 155, 133, 0.08);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.05);
}

.question-icon {
  font-size: 1.5rem;
  margin-right: 1rem;
  flex-shrink: 0;
}

.question-text {
  color: #2D3E40; /*  */
  font-size: 1rem;
  line-height: 1.6;
  margin: 0;
  font-weight: 500; /*  */
}

/* ===== UI ===== */

/*  */
@keyframes subtle-pulse {
  0% { 
    transform: scale(1); 
    opacity: 1; 
  }
  50% { 
    transform: scale(1.02); 
    opacity: 0.9; 
  }
  100% { 
    transform: scale(1); 
    opacity: 1; 
  }
}

/*  */
.back-to-overview-container:not(.controls-hidden) .back-to-overview-btn {
  animation: subtle-pulse 2s ease-in-out;
}

.bottom-hint:not(.controls-hidden) .companion-hint {
  animation: subtle-pulse 2s ease-in-out 0.2s;
}

/*  */
@media (max-width: 768px) {
  .back-to-overview-container {
    top: 60px;
  }
  
  .bottom-hint {
    bottom: 20px;
  }
  
  /*  */
  .back-to-overview-container.controls-hidden,
  .bottom-hint.controls-hidden {
    transition: 
      opacity 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.05s,
      transform 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.05s,
      visibility 0s 0.35s;
  }
}

/*  */
@media (prefers-reduced-motion: reduce) {
  .back-to-overview-container,
  .bottom-hint {
    transition: opacity 0.2s ease, visibility 0.2s ease;
  }
  
  .back-to-overview-container.controls-hidden,
  .bottom-hint.controls-hidden {
    transition: opacity 0.2s ease, visibility 0.2s ease;
  }
  
  .back-to-overview-container:not(.controls-hidden) .back-to-overview-btn,
  .bottom-hint:not(.controls-hidden) .companion-hint {
    animation: none;
  }
}

/* =====  ===== */
.bottom-hint {
  position: fixed;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;
  
  /*  */
  opacity: 1;
  visibility: visible;
  transition: 
    opacity 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94),
    transform 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94),
    visibility 0.4s ease;
  
  /*  */
  transform: translateX(-50%) translateY(0);
}

/*  */
.bottom-hint.controls-hidden {
  opacity: 0;
  visibility: hidden;
  transform: translateX(-50%) translateY(15px);
  pointer-events: none;
  
  /*  */
  transition: 
    opacity 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.1s,
    transform 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.1s,
    visibility 0s 0.7s;
}

.companion-hint {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  background: rgba(255, 255, 255, 0.98) !important; /*  */
  color: #000000 !important; /*  */
  padding: 1rem 2rem;
  border-radius: 30px;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.15);
  font-weight: 700 !important; /*  */
  border: 2px solid rgba(0, 0, 0, 0.3) !important; /*  */
  z-index: 999 !important; /*  */
  position: relative !important; /*  */
  backdrop-filter: blur(15px) !important; /*  */
}

.companion-hint .hint-text {
  color: #000000 !important;
  font-weight: 700 !important;
  font-size: 1rem !important; /*  */
  line-height: 1.4 !important;
}

.companion-hint .hint-icon {
  color: #000000 !important;
  font-size: 1.2rem !important;
}

.companion-hint:hover {
  transform: translateY(-3px);
  box-shadow: 0 12px 40px rgba(84, 169, 140, 0.4);
}

.companion-disabled {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  background: rgba(255, 255, 255, 0.98) !important; /*  */
  color: #000000 !important; /*  */
  padding: 1rem 2rem;
  border-radius: 30px;
  backdrop-filter: blur(20px);
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.15); /*  */
  border: 2px solid rgba(0, 0, 0, 0.3) !important; /*  */
  z-index: 999 !important; /*  */
  position: relative !important; /*  */
}

.companion-disabled p {
  margin: 0;
  font-weight: 700 !important; /*  */
  color: #000000 !important; /*  */
  font-size: 1rem !important;
  line-height: 1.4 !important;
}

.companion-disabled .rest-icon {
  color: #000000 !important;
  font-size: 1.2rem !important;
}

/* =====  ===== */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  backdrop-filter: blur(5px);
  padding: 20px;
}

.modal-dialog {
  background: white;
  border-radius: 30px;
  width: 100%;
  max-width: 500px;
  max-height: 80vh;
  display: flex;
  flex-direction: column;
  box-shadow: 0 25px 60px rgba(0, 0, 0, 0.2);
  overflow: hidden;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 2rem;
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}

.modal-header h3 {
  color: #2D3E40;
  font-size: 1.4rem;
  font-weight: 500;
  margin: 0;
}

.modal-close {
  background: none;
  border: none;
  font-size: 2rem;
  color: #8B9A9B;
  cursor: pointer;
  transition: all 0.3s ease;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}

.modal-close:hover {
  background: rgba(0, 0, 0, 0.05);
  color: #2D3E40;
}

.modal-body {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.modal-messages {
  flex: 1;
  overflow-y: auto;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.modal-message {
  max-width: 80%;
  padding: 1rem 1.5rem;
  border-radius: 20px;
  font-size: 0.95rem;
  line-height: 1.6;
}

.modal-message.user {
  background: linear-gradient(135deg, #84A98C 0%, #52796F 100%);
  color: white;
  align-self: flex-end;
  border-bottom-right-radius: 5px;
}

.modal-message.companion {
  background: rgba(0, 0, 0, 0.05);
  color: #2D3E40;
  align-self: flex-start;
  border-bottom-left-radius: 5px;
}

/*  */
.typing-indicator {
  align-self: flex-start;
  margin-bottom: 1rem;
}

.typing-message {
  display: flex;
  align-items: flex-end;
  gap: 0.8rem;
  animation: fadeIn 0.3s ease;
}

.typing-avatar {
  font-size: 1.5rem;
  flex-shrink: 0;
}

.typing-content {
  background: rgba(0, 0, 0, 0.05);
  border-radius: 20px;
  border-bottom-left-radius: 5px;
  padding: 1rem 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.typing-text {
  color: var(--text-secondary);
  font-size: 0.9rem;
  font-style: italic;
}

.typing-dots {
  display: flex;
  gap: 4px;
  align-items: center;
}

.typing-dots span {
  width: 6px;
  height: 6px;
  background: var(--primary-color);
  border-radius: var(--radius-full);
  animation: typing-bounce 1.4s ease-in-out infinite;
}

.typing-dots span:nth-child(1) {
  animation-delay: 0s;
}

.typing-dots span:nth-child(2) {
  animation-delay: 0.2s;
}

.typing-dots span:nth-child(3) {
  animation-delay: 0.4s;
}

@keyframes typing-bounce {
  0%, 80%, 100% {
    transform: scale(1);
    opacity: 0.5;
  }
  40% {
    transform: scale(1.3);
    opacity: 1;
  }
}

.modal-form {
  display: flex;
  gap: 1rem;
  padding: 1.5rem 2rem;
  border-top: 1px solid rgba(0, 0, 0, 0.05);
}

.modal-input {
  flex: 1;
  padding: 1rem 1.5rem;
  border: 2px solid rgba(132, 169, 140, 0.2);
  border-radius: 25px;
  font-size: 0.95rem;
  transition: all 0.3s ease;
  background: white;
  color: #2D3E40;
  outline: none;
  /*  */
  box-shadow: none;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
}

.modal-input:focus {
  outline: none;
  border-color: #84A98C;
}

.modal-send {
  padding: 1rem 2rem;
  background: linear-gradient(135deg, #84A98C 0%, #52796F 100%);
  color: white;
  border: none;
  border-radius: 25px;
  font-size: 0.95rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
}

.modal-send:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(84, 169, 140, 0.3);
}

.modal-send:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/*  */
.btn-loading {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.btn-spinner {
  width: 16px;
  height: 16px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-left: 2px solid white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* =====  ===== */
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.3s ease;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}

.slide-up-enter-active,
.slide-up-leave-active {
  transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

.slide-up-enter-from {
  opacity: 0;
  transform: translateY(30px);
}

.slide-up-leave-to {
  opacity: 0;
  transform: translateY(-30px);
}

.fade-slide-enter-active,
.fade-slide-leave-active {
  transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

.fade-slide-enter-from {
  opacity: 0;
  transform: translateY(20px);
}

.fade-slide-leave-to {
  opacity: 0;
  transform: translateY(-20px);
}

.message-enter-active {
  transition: all 0.3s ease;
}

.message-enter-from {
  opacity: 0;
  transform: translateX(-20px);
}

.modal-enter-active,
.modal-leave-active {
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.modal-enter-from,
.modal-leave-to {
  opacity: 0;
}

.modal-enter-from .modal-dialog,
.modal-leave-to .modal-dialog {
  transform: scale(0.9) translateY(20px);
}

