/* CSS */
:root {
  --breakpoint-xs: 375px;
  --breakpoint-sm: 480px;
  --breakpoint-md: 768px;
  --breakpoint-lg: 1024px;
  --touch-target-min: 44px; /*  */
}

/*  */
.emotion-tag,
.modal-close,
.back-to-overview-btn,
.back-to-selection-btn,
.retry-btn,
.dismiss-btn,
