  .emotion-tag:active,
  .flowerpot-item:active,
  .back-to-overview-btn:active,
  .back-to-selection-btn:active,
  .companion-hint:active {
    transform: scale(0.98);
    transition: transform 0.1s ease;
  }
  
  /*  */
