// services/llm.service.js
import { ApiError } from './ApiError';

class LLMService {
  constructor() {
    // API
    this.baseUrl = process.env.NODE_ENV === 'production' 
      ? '/api/llm-proxy' 
      : 'http://localhost:3001/api/llm-proxy';
    this.maxRetries = 3;
    this.retryDelay = 1000; // 1
    this.vueInstance = null; // Vue
  }

  /**
   * Vue
   * @param {Object} vueInstance Vue
   */
  setVueInstance(vueInstance) {
    this.vueInstance = vueInstance;
  }

  /**
   * API
   * @param {Object} vueInstance Vue
   * @returns {Object} API
   */
  getApiConfig(vueInstance = null) {
    let store = null;
    
    // store
    if (vueInstance && vueInstance.$store) {
      store = vueInstance.$store.state;
    } else if (window.$store) {
      store = window.$store.state;
    } else if (window.vueApp && window.vueApp.config.globalProperties.$store) {
      store = window.vueApp.config.globalProperties.$store.state;
    }
    
    if (!store) {
      console.warn('[LLM] store');
      return {
        apiKey: '',
        apiUrl: 'https://dashscope.aliyuncs.com/compatible-mode/v1/chat/completions',
        model: 'qwen-turbo'
      };
    }

    // 
    const configMode = store.configMode || 'cloud';
    console.log('[LLM] :', configMode);
    
    let apiKey, apiUrl, model;
    
    if (configMode === 'cloud') {
      // 
      apiKey = store.appConfig?.llm?.apiKey || '';
      apiUrl = store.appConfig?.llm?.apiUrl || 'https://dashscope.aliyuncs.com/compatible-mode/v1/chat/completions';
      model = store.appConfig?.llm?.modelName || 'qwen-turbo';
    } else {
      // 
      apiKey = store.apiConfig?.apiKey || '';
      apiUrl = store.apiConfig?.endpoint || store.apiConfig?.apiUrl || 'https://dashscope.aliyuncs.com/compatible-mode/v1/chat/completions';
      model = store.apiConfig?.model || 'qwen-turbo';
    }

    //  -  '0', 'undefined', 'null' 
    const validModel = model && 
                      typeof model === 'string' && 
                      model.length > 0 && 
                      model !== '0' && 
                      model !== 'undefined' && 
                      model !== 'null' && 
                      !model.match(/^\d+$/) ? model : 'qwen-turbo';

    console.log('[LLM] API:', {
      configMode,
      hasApiKey: !!apiKey,
      apiUrl,
      model: validModel,
      rawModel: model
    });

    return {
      apiKey,
      apiUrl,
      model: validModel
    };
  }

  /**
   * LLM
   * @param {Object} options 
   * @param {number} retryCount 
   * @returns {Promise<Object>} 
   */
  async sendRequest(options, retryCount = 0) {
    try {
      const config = this.getApiConfig(this.vueInstance);
      
      console.log(`[LLM]  ( ${retryCount + 1}/${this.maxRetries + 1})`, {
        messageCount: options.messages?.length,
        firstMessage: options.messages?.[0]?.content?.substring(0, 50) + '...',
        hasApiKey: !!config.apiKey,
        apiUrl: config.apiUrl,
        baseUrl: this.baseUrl
      });

      // 
      const requestUrl = this.baseUrl;
      const requestHeaders = {
        'Content-Type': 'application/json',
        'X-API-Key': config.apiKey || '',
        'X-API-URL': config.apiUrl || '',
        'X-Model-Name': config.model || 'qwen-turbo'
      };

      // 
      const requestBody = JSON.stringify({
        ...options,
        model: config.model || 'qwen-turbo'
      });

      const response = await fetch(requestUrl, {
        method: 'POST',
        headers: requestHeaders,
        body: requestBody
      });

      // 
      const contentType = response.headers.get('content-type');
      if (!contentType?.includes('application/json')) {
        const text = await response.text();
        console.error('[LLM] JSON:', {
          status: response.status,
          contentType,
          text: text.substring(0, 200)
        });
        throw new ApiError(
          'JSON',
          'INVALID_RESPONSE_TYPE',
          { contentType, preview: text.substring(0, 100) },
          response.status
        );
      }

      // 
      const data = await response.json();

      // 
      if (!response.ok) {
        throw ApiError.fromResponse(response, data);
      }

      // 
      if (!data.content && !data.choices?.[0]?.message?.content) {
        throw new ApiError(
          '',
          'INVALID_RESPONSE_FORMAT',
          {
            received: Object.keys(data),
            expected: ['content', 'choices[0].message.content']
          },
          response.status
        );
      }

      return {
        content: data.content || data.choices[0].message.content,
        usage: data.usage,
        model: data.model
      };

    } catch (error) {
      // 
      console.error('[LLM] :', {
        attempt: retryCount + 1,
        error: {
          name: error.name,
          message: error.message,
          code: error.code,
          status: error.status
        }
      });

      // 404API
      if (error.message?.includes('404') || error.status === 404) {
        throw new ApiError(
          '',
          'API_ENDPOINT_NOT_FOUND',
          {
            baseUrl: this.baseUrl,
            suggestion: ''
          },
          404
        );
      }

      // 
      const shouldRetry = this.shouldRetryError(error) && retryCount < this.maxRetries;
      
      if (shouldRetry) {
        console.log(`[LLM]  ${this.retryDelay}ms ...`);
        await new Promise(resolve => setTimeout(resolve, this.retryDelay));
        return this.sendRequest(options, retryCount + 1);
      }

      // ApiError
      throw error instanceof ApiError ? error : ApiError.fromError(error);
    }
  }

  /**
   * 
   * @param {Error} error 
   * @returns {boolean}
   */
  shouldRetryError(error) {
    // 
    if (!error.status || error.status >= 500) return true;
    
    // 
    const nonRetryableCodes = [
      'INVALID_API_KEY',
      'INVALID_REQUEST',
      'INVALID_RESPONSE_FORMAT'
    ];
    
    return !nonRetryableCodes.includes(error.code);
  }

  /**
   * 
   * @param {Object} thoughtRecord 
   * @returns {Promise<string>} 
   */
  async analyzeThought(thoughtRecord) {
    const systemPrompt = {
      role: "system",
      content: `(CBT)





[2-3]


1. []: []
2. []: []
(1-3)


1. []
2. []
3. []


[]

`
    };

    const userMessage = {
      role: "user",
      content: `${thoughtRecord.emotions.join('')}
${thoughtRecord.situation || '()'}
${thoughtRecord.automaticThought}`
    };

    try {
      console.log('[LLM] :', {
        emotions: thoughtRecord.emotions,
        hasThought: !!thoughtRecord.automaticThought,
        hasSituation: !!thoughtRecord.situation
      });

      const response = await this.sendRequest({
        messages: [systemPrompt, userMessage],
        temperature: 0.7,
        max_tokens: 1000
      });

      return response.content;

    } catch (error) {
      console.error('[LLM] :', error);
      throw new ApiError(
        '',
        'ANALYSIS_FAILED',
        error.details,
        error.status
      );
    }
  }

  /**
   * 
   * @param {Array} chatHistory 
   * @param {string} userMessage 
   * @param {Object} context 
   * @returns {Promise<string>} 
   */
  async continueChat(chatHistory, userMessage, context = {}) {
    // 
    const analysisContext = context.analysis ? this.formatAnalysisContext(context.analysis) : '';
    const emotionalContext = this.buildEmotionalContext(context);
    
    const systemPrompt = {
      role: "system",
      content: `(CBT)


## 
${emotionalContext}

## 
${analysisContext}

## 
1. ****: 
2. ****: 
3. ****: 
4. ****: 
5. ****: 2-3
6. **CBT**: CBT

`
    };

    const messages = [
      systemPrompt,
      ...chatHistory,
      { role: "user", content: userMessage }
    ];

    try {
      console.log('[LLM] :', {
        historyLength: chatHistory.length,
        newMessage: userMessage.substring(0, 50) + '...',
        context: {
          emotions: context.emotions,
          hasThought: !!context.thought,
          hasSituation: !!context.situation,
          biasCount: context.analysis?.cognitiveBiases?.length
        }
      });

      const response = await this.sendRequest({
        messages,
        temperature: 0.7,
        max_tokens: 500
      });

      return response.content;

    } catch (error) {
      console.error('[LLM] :', error);
      throw new ApiError(
        '',
        'CHAT_FAILED',
        error.details,
        error.status
      );
    }
  }

  /**
   * 
   * @param {Object} analysis 
   * @returns {string} 
   */
  formatAnalysisContext(analysis) {
    if (!analysis) return '';
    
    const parts = [];
    
    // 
    if (analysis.empathy) {
      parts.push(`****: ${analysis.empathy}`);
    }
    
    // 
    if (analysis.cognitiveBiases && analysis.cognitiveBiases.length > 0) {
      const biases = analysis.cognitiveBiases.map(bias => 
        `  - ${bias.label}: ${bias.description}`
      ).join('\n');
      parts.push(`****:\n${biases}`);
    }
    
    // 
    if (analysis.guidingQuestions && analysis.guidingQuestions.length > 0) {
      const questions = analysis.guidingQuestions.map((q, index) => 
        `  ${index + 1}. ${q.text}`
      ).join('\n');
      parts.push(`****:\n${questions}`);
    }
    
    return parts.join('\n\n') || '';
  }

  /**
   * 
   * @param {Object} context 
   * @returns {string} 
   */
  buildEmotionalContext(context) {
    const parts = [];
    
    // 
    if (context.emotions && context.emotions.length > 0) {
      parts.push(`****: ${context.emotions.join('')}`);
    }
    
    // 
    if (context.situation) {
      parts.push(`****: ${context.situation}`);
    }
    
    // 
    if (context.thought) {
      parts.push(`****: "${context.thought}"`);
    }
    
    // 
    if (context.timestamp) {
      const timeStr = new Date(context.timestamp).toLocaleString('zh-CN');
      parts.push(`****: ${timeStr}`);
    }
    
    return parts.join('\n') || '';
  }
}

// 
export default new LLMService(); 